:root {
  --softBlue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --white: hsl(0, 0%, 100%);
  --dark-bg: hsl(217, 54%, 11%);
  --dark-bg-card: hsl(216, 50%, 16%);
  --dark-line: hsl(215, 32%, 27%);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-outfit);
  font-size: 1em;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, :active {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  max-width: 68.75em;
  padding: 0 1.5em;
}
@media (min-width: 64em) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1.125em 1.125em;
  background-color: var(--dark-bg-card);
  width: 20.4375em;
  height: 32.5em;
  border-radius: 0.625em;
  margin: 0 auto;
  color: var(--softBlue);
  box-shadow: 0 0 1.5em var(--dark-bg);
}
.card .cardImage {
  background: url(/images/image-equilibrium.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 0.625em;
  width: 100%;
  height: 18.125em;
  cursor: pointer;
}
.card .cardImage .colorOverlay {
  border-radius: 0.625em;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.card .cardImage .colorOverlay img {
  display: none;
  width: 20%;
  height: 20%;
}
.card .cardImage:hover .colorOverlay {
  background: rgba(0, 255, 247, 0.3);
}
.card .cardImage:hover img {
  display: block;
}
.card .cardTitle {
  font-weight: 600;
  margin: 0.625em 0;
  color: var(--white);
}
.card .cardTitle:hover {
  color: var(--cyan);
  cursor: pointer;
}
.card .cardDescription {
  font-weight: 300;
  margin: 0.625em 0;
}
.card .cardDetails {
  display: flex;
  justify-content: space-between;
  align-content: center;
}
.card .cardDetails img {
  height: 1.25em;
  margin-right: 0.625em;
}
.card .cardDetails .cardPrice {
  display: flex;
  color: var(--cyan);
  font-weight: 600;
}
.card .cardDetails .cardTimer {
  display: flex;
}
.card .cardLine {
  border: 0.0625em solid var(--dark-line);
  margin: 1.5em 0;
}
.card .cardAuthor {
  display: flex;
  align-items: center;
}
.card .cardAuthor img {
  width: 10%;
  border: 0.0625em solid var(--white);
  border-radius: 50%;
}
.card .cardAuthor span {
  margin: 0 1em;
}
.card .cardAuthor span a {
  color: var(--softBlue);
}
.card .cardAuthor span a:hover {
  color: var(--cyan);
}

body {
  background-color: var(--dark-bg);
  color: var(--white);
}

footer {
  margin-top: 1.125em;
}

.header {
  margin-top: 0.5em;
}